<template>
 <div class="center newCenter">
     <div class="center_breadcrumb">
        <crumb>
            <div @click="openAdd" class="new--btn" slot="right">+ 新增</div>
        </crumb>
    </div>
    <div class="center_content">
        <div class="center_content_search">
            <div class="center_content_search_left">
                <div class="search-input-suffix">
                    <label>类型名称：</label>
                    <el-input class="search"
                            @keyup.enter.native="search"
                            v-model="page.keyWord"
                            placeholder="请输入类型名称"
                            >
                    </el-input>
                </div>
          <div class="search--btn" style="margin: 0 0 0 20px" @click="search">搜索</div>
            </div>
        </div>
         <div class="table-container">
             <el-table
                :data="tableData"
                stripe
                style="width: 100%">
                <el-table-column
                prop=""
                width="100"
                align="center"
                label="类型编号">
                <template slot-scope="scope">
                    {{scope.$index +1}}
                </template>
                </el-table-column>
                <el-table-column
                prop="classifyName"
                label="类型名称">
                </el-table-column>
                <el-table-column
                prop="remarks"
                label="类型简介">
                </el-table-column>
                <el-table-column
                label="操作"
                width="160">
                <template slot-scope="scope">                  
                    <div class="operate-button distance">
                        <SvgIcon icon-class="iconbianji1"></SvgIcon> 
                        <el-button
                        @click.native.prevent="editRow(scope.row)"
                        type="text"
                        size="small">
                        编辑
                        </el-button>
                    </div>
                    <div class="operate-button">
                        <SvgIcon icon-class="iconshanchu"></SvgIcon>
                        <el-button
                        @click.native.prevent="deleteRow(scope.row)"
                        type="text"
                        size="small"
                        style="color: #ff5858">
                        删除
                        </el-button>
                    </div>                    
                </template>
                </el-table-column>
            </el-table>
            <div class="center_pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.pageSize"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="data.total">
                </el-pagination>
            </div>
        </div>
    </div>
     <!-- 新增编辑 -->
    <el-dialog :title="dialogTitle" :visible.sync="userDialogFormVisible" :before-close="handleDialogClose" class="set-el-dialog" :close-on-click-modal='false'>
        <el-form :model="form" :rules="rules" ref="form" label-width="100px">
            <el-form-item label="类型名称" prop="classifyName">
                <el-input v-model="form.classifyName"  placeholder=""></el-input>
            </el-form-item>
             <el-form-item label="类型简介" prop="remarks">
                <el-input
                    v-model="form.remarks"
                    type="textarea"
                    :rows="2"
                    placeholder="">
                    </el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button  @click="cancel('form')">取 消</el-button>
            <el-button type="primary" @click="create('form')">确 定</el-button>
        </div>
    </el-dialog>
 </div>
</template>

<script>
import systemApi from '@/api/systemApi'
export default {
 name: 'QuestionnaireTypes',
 data() {
 return {
    pageTitle:'问卷类型管理',
    data:{},
    tableData: [],
    userDialogFormVisible:false,
    isAdd:true,
    form: {
        classifyName: '',
        remarks: '',
        id:undefined
    },
    page:{
        keyWord:'',
        page:1,
        pageSize:15
    },
    rules:{
        classifyName: [
            {
              required: true,
              message: '请输入类型名称',
              trigger: 'blur'
            }
        ],
        remarks: [
            {
              required: true,
              message: '请输入类型简介',
              trigger: 'blur'
            }
        ]
    },
    api:{
        listPage:'/form/rest/FormClassifyBusiness/listPage/v1',
        add:'/form/rest/FormClassifyBusiness/add/v1',
        edit:'/form/rest/FormClassifyBusiness/modify/v1',
        del:'/form/rest/FormClassifyBusiness/remove/v1'
    }
 }
 },
 created(){},
 mounted(){
     this.initData()
 },
 computed:{
     dialogTitle(){
        return this.isAdd ? '添加问卷类型' : '编辑问卷类型'
     }
 },
 methods:{
     initData(){
         this.$post(this.api.listPage,this.page).then(res =>{
             if(res.code === 200){
                 this.data=res.data;
                 this.tableData=res.data.rows;                 
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: '获取列表失败'
                })
             }
         })
     },
     search(){
        this.resetTemp();
        this.initData() ;
     },
     handleSizeChange(val){
        this.page.pageSize = val;
		this.initData()
     },
     handleCurrentChange(obj){
        this.page.page = obj;
        this.initData()
     },
     openAdd(){         
          this.userDialogFormVisible = true;
          this.form={
                classifyName: '',
                remarks: '',
                id:undefined
        };       
        this.isAdd=true;
     },
   deleteRow(row){
        this.$confirm('确定删除该类型?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
           this.userDel(row.id)
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
        });        
     },
     editRow(row){
        const obj={
                classifyName: row.classifyName,
                remarks: row.remarks,
                id:row.id
        }
        this.form=obj;
        this.userDialogFormVisible = true; 
        this.isAdd=false;
     },
     create(formName){            
       this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.isAdd){
                  this.userAdd()
            }else{   
                 this.userEdit()             
                
            }           
                     
          } 
        });
     },
     cancel(formName){
        this.userDialogFormVisible = false;
        this.$refs[formName].resetFields();
     },
     handleDialogClose(){
        this.$refs['form'].resetFields();
        this.userDialogFormVisible = false;    
     },
     userAdd(){
       this.$post(this.api.add,this.form)
            .then( res => {
                    if(res.code === 200){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: '添加成功'
                            
                            })
                        this.userDialogFormVisible = false;
                        this.resetTemp()
                        this.initData() ;
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.msg
                    })
                    }
                    
            })
     },
     userDel(id){
       const postData ={
           id:id
       };
        this.$post(this.api.del,postData)
        .then( res => {
                if(res.code === 200){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: '删除成功'
                        
                        })
                    this.initData() ;
                }else{
                    this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.msg
                })
                }
                
        })
     },
     userEdit(){
        this.$post(this.api.edit,this.form)
            .then( res => {
                    if(res.code === 200){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: '修改成功'
                            
                            })
                        this.userDialogFormVisible = false;
                        this.initData() ;
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.msg
                    })
                    }
                    
            })
     },
    resetTemp(){
        this.page.page=1;
        this.page.pageSize=15;
     }
    
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss';
    .search{
        .el-input__suffix{
            top:5px;
        }
    }
    .newCenter{
        .el-input__inner{
            width: 95%;
        }
        .el-textarea__inner{
            width: 95%;
        }
    }
</style>



